<template>
  <tas-base-crud ref="sub-module" :config="config" hide-create hide-delete hide-update hide-filter hide-detail>
    <template v-slot:list-table-action="data">
      <a @click="openModal('modal-rincian-presence', data)" class="btn btn-info btn-sm">Detail Presensi</a>
    </template>
    <template v-slot:list-header>
      <b-modal header-class="pb-1" v-if="modalData.detail" id="modal-rincian-presence" hide-footer size="xl">
        <template #modal-header>
          <div class="d-flex justify-content-between flex-fill">
            <div class="d-flex flex-column flex-fill">
              <div class="text-center mb-5">
                <h5 class="mb-1 font-weight-bold">Detail Presensi Tenaga Kerja</h5>
                <span class="font-weight-bolder">Periode {{ modalData.detail.periode_month | parse('period') }}</span>
              </div>
              <table class="table no-border mb-0">
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">Tenaga Kerja</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.rel_employee_id }}</td>
                  <td class=""></td>
                  <td class="pt-0 pl-0 nowrap-table">Hari Kerja</td>
                  <th class="pt-0 pr-0 nowrap-table">
                    <span class="text-right font-weight-bolder">:&emsp;{{ modalData.detail.work_day_count  || 0 | parse('number') }} hari</span>
                  </th>
                </tr>
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">Jabatan</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.rel_job_position_id }}</td>
                  <td class=""></td>
                  <td class="pt-0 pl-0 nowrap-table">Tidak Masuk</td>
                  <th class="pt-0 pr-0 nowrap-table">
                    <span class="text-right font-weight-bolder">:&emsp;{{ modalData.detail.work_day_lost_count  || 0 | parse('number') }} hari</span>
                  </th>
                </tr>
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">Status Ketenagakerjaan</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.rel_status_employment_id }}</td>
                  <td class=""></td>
                  <td class="pt-0 pl-0 nowrap-table">Terlambat</td>
                  <th class="pt-0 pr-0 nowrap-table">
                    <span class="text-right font-weight-bolder">:&emsp;{{ modalData.detail.work_day_late_count  || 0 | parse('number') }} hari</span>
                  </th>
                </tr>
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">TapTap User ID</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.taptap_user_id }}</td>
                  <td class=""></td>
                  <td class="pt-0 pl-0 nowrap-table">Menit Terlambat</td>
                  <th class="pt-0 pr-0 nowrap-table">
                    <span class="text-right font-weight-bolder">:&emsp;{{ modalData.detail.work_day_minute_late_count  || 0 | parse('number') }} menit</span>
                  </th>
                </tr>
              </table>
            </div>
          </div>
        </template>
        <div v-if="modalData.detail" class="row">
          <div class="col-7">
            <!-- <h5>Data Presensi TapTap</h5> -->
            <b-tabs content-class="mt-3">
              <b-tab title="Data Presensi TapTap Bulan ini" active>
                <div class="scrolling-wrapper">
                  <table class="table table-custom-header v-center table-bordered scrolling-tbody">
                    <thead class="bg-light-primary">
                      <tr>
                        <th class="text-center">Tanggal</th>
                        <th class="text-center">Shift</th>
                        <th class="text-center">Masuk</th>
                        <th class="text-center">Keluar</th>
                        <th class="text-center">Terlambat</th>
                        <th class="text-center">Status</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(t,i) in modalData.sync.data_taptap" :key="i+'-taptap-recaps'">
                        <td class="nowrap-table">{{ t.series_periode | parse('longDate') }}</td>
                        <td class="nowrap-table text-capitalize">{{ t.rel_working_category_id }}</td>
                        <td class="nowrap-table">{{ t.checkin }}</td>
                        <td class="nowrap-table">{{ t.checkout }}</td>
                        <td class="nowrap-table">{{ t.late | parse('hours') }}</td>
                        <td class="nowrap-table text-capitalize">{{ t.status_presence }}</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </b-tab>
              <b-tab title="Presensi Bulan lalu">
                <div class="scrolling-wrapper">
                  <table class="table table-custom-header v-center table-bordered scrolling-tbody">
                    <thead class="bg-light-primary">
                      <tr>
                        <th class="text-center">Tanggal</th>
                        <th class="text-center">Shift</th>
                        <th class="text-center">Masuk</th>
                        <th class="text-center">Keluar</th>
                        <th class="text-center">Terlambat</th>
                        <th class="text-center">Status</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(t,i) in modalData.sync.data_taptap_month_before" :key="i+'-taptap-recaps'">
                        <td class="nowrap-table">{{ t.series_periode | parse('longDate') }}</td>
                        <td class="nowrap-table text-capitalize">{{ t.rel_working_category_id }}</td>
                        <td class="nowrap-table">{{ t.checkin }}</td>
                        <td class="nowrap-table">{{ t.checkout }}</td>
                        <td class="nowrap-table">{{ t.late | parse('hours') }}</td>
                        <td class="nowrap-table text-capitalize">{{ t.status_presence }}</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </b-tab>
            </b-tabs>
          </div>
          <div class="col-5">
            <table class="table table-custom-header v-center table-bordered">
              <tbody>
                <tr>
                  <td colspan="4" class="font-weight-bolder bg-light-primary text-uppercase">Rekap Presensi Tenaga Kerja</td>
                </tr>
                <tr>
                  <th>Komponen</th>
                  <th>Nilai</th>
                  <th class="text-center nowrap-table">Presensi TapTap</th>
                </tr>
                <tr>
                  <td class="nowrap-table">Hari Kerja</td>
                  <td class="bg-light-primary">
                    <s-input v-model="modalData.detail.work_day_count" :item="{ type:'number', validation:[], suffix: 'Hari', label:null, nm:true, ic:'ml-4', api:'' }" />
                  </td>
                  <td class="text-center nowrap-table"> {{ modalData.sync.data_recaps.work_day_count }} Hari </td>
                </tr>
                <tr>
                  <td class="nowrap-table">Tidak Masuk</td>
                  <td class="bg-light-primary">
                    <s-input v-model="modalData.detail.work_day_lost_count" :item="{ type:'number', validation:[], suffix: 'Hari', label:null, nm:true, ic:'ml-4', api:'' }" />
                  </td>
                  <td class="text-center nowrap-table"> {{ modalData.sync.data_recaps.work_day_lost_count }} Hari </td>
                </tr>
                <tr>
                  <td class="nowrap-table">Terlambat</td>
                  <td class="bg-light-primary">
                    <s-input v-model="modalData.detail.work_day_late_count" :item="{ type:'number', validation:[], suffix: 'Hari', label:null, nm:true, ic:'ml-4', api:'' }" />
                  </td>
                  <td class="text-center nowrap-table"> {{ modalData.sync.data_recaps.work_day_late_count }} Hari </td>
                </tr>
                <tr>
                  <td class="nowrap-table">Menit Terlambat</td>
                  <td class="bg-light-primary">
                    <s-input v-model="modalData.detail.work_day_minute_late_count" :item="{ type:'number', validation:[], suffix: 'Menit', label:null, nm:true, ic:'ml-4', api:'' }" />
                  </td>
                  <td class="text-center nowrap-table"> {{ modalData.sync.data_recaps.work_day_minute_late_count }} Menit </td>
                </tr>
                <tr>
                  <td colspan="3">
                    <a @click="copyValue()" class="btn btn-block btn-light-primary mt-3"><i class="ri-file-copy-line"></i> Salin nilai Presensi TapTap </a>
                  </td>
                </tr>
              </tbody>
            </table>
            <div class="form-group mb-5 mt-0">
              <label>Deskripsi Rekap Presensi</label>
              <textarea v-model="modalData.detail.description" class="form-control" rows="3" placeholder="Deskripsi / note Rekap Presensi"></textarea>
            </div>
            <div class="mt-5">
              <button @click="submitData('modal-rincian-bills')" class="btn btn-primary btn-lg btn-block font-size-md">Perbarui &amp; Simpan</button>
            </div>
          </div>
        </div>
      </b-modal>
    </template>
  </tas-base-crud>
</template>

<style scoped>
  .scrolling-wrapper {
    max-height: 500px;
    overflow-x: auto;
    overflow-y: auto;
  }
  .scrolling-tbody {
    width: 100%;
    margin-bottom: 0;
  }

  /*This will work on every browser but Chrome Browser*/
  .scrolling-tbody thead {
    position: sticky;
    position: -webkit-sticky;
    top: 0;
    z-index: 999;
  }

  /*This will work on every browser*/
  .scrolling-tbody thead th {
    position: sticky;
    position: -webkit-sticky;
    top: 0;
    z-index: 999;
  }
</style>

<script>
export default {
  name: 'crud-presence-on-periode-recaps',
  data () {
    return {
      modalData: {},
      mainKey: 0,
      config: {
        title: 'Presensi Tenaga Kerja',
        model_api: null,
        getter: 'custom/presence-on-periode-recaps',
        setter: 'custom/presence-on-periode-recaps',
        pk_field: 'name',
        filter_api: {},
        custom_api: {},
        permission: {
          create: 'create-presence-on-periode-recaps',
          read: 'view-presence-on-periode-recaps',
          update: 'update-presence-on-periode-recaps',
          delete: 'delete-presence-on-periode-recaps'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          {
            id: 'status_code',
            label: 'Sinkronisasi',
            methods: {
              list: {
                order: true,
                class: { 'not_synchronized_yet': 'badge badge-danger', 'synchronized': 'badge badge-primary' },
                transform: 'sync'
              }
            }
          },
          {
            id: 'employee_id',
            label: 'Nama Tenaga Kerja',
            methods: {
              list: { view_data: 'rel_employee_id' },
              create: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              update: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              filter: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              }
            }
          },
          {
            id: 'job_position_id',
            label: 'Jabatan',
            methods: {
              list: { view_data: 'rel_job_position_id' },
              create: {
                setter: 'job-positions',
                getter: 'job-positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              },
              update: {
                setter: 'job-positions',
                getter: 'job-positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              },
              filter: {
                setter: 'job-positions',
                getter: 'job-positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              }
            }
          },
          {
            id: 'status_employment_id',
            label: 'Status Ketenagakerjaan',
            methods: {
              list: { view_data: 'rel_status_employment_id' },
              create: {
                setter: 'custom/master-status-employments',
                getter: 'custom/master-status-employments',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  },
                  get_param: [{ 'id': 'job_position_id' }]
                }
              },
              update: {
                setter: 'custom/master-status-employments',
                getter: 'custom/master-status-employments',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  },
                  get_param: [{ 'id': 'job_position_id' }]
                }
              },
              filter: {
                setter: 'custom/master-status-employments',
                getter: 'custom/master-status-employments',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  },
                  get_param: [{ 'id': 'job_position_id' }]
                }
              }
            }
          },
          { id: 'work_day_count', label: 'Jumlah Hari Kerja', methods: { create: false, update: false, list: { transform: 'number' }, detail: { transform: 'number' } } },
          { id: 'work_day_lost_count', label: 'Tidak Masuk (Hari)', methods: { create: false, update: false, list: { transform: 'number' }, detail: { transform: 'number' } } },
          { id: 'work_day_late_count', label: 'Terlambat (Hari)', methods: { create: false, update: false, list: { transform: 'number' }, detail: { transform: 'number' } } },
          { id: 'work_day_minute_late_count', label: 'Terlambat (Menit)', methods: { create: false, update: false, list: { transform: 'number' }, detail: { transform: 'number' } } }
        ]
      }
    }
  },
  watch: {
    mainKey: {
      immediate: false,
      deep: false,
      handler (e) {
        this.$refs['sub-module'].$children[1].getData()
      }
    }
  },
  methods: {
    async openModal (id, data) {
      const detail = {
        detail: await this.getDetail('get', 'custom/presence-on-periode-recaps/show', { id: data.rowData.id })
      }
      let syncData = {
        id: data.rowData.id,
        periode_month: detail.detail.periode_month,
        taptap_user_id: detail.detail.taptap_user_id,
        start_date: detail.detail.start_date,
        end_date: detail.detail.end_date
      }
      detail.sync = await this.getDetail('post', 'custom/presence-on-periode-recaps/sync', syncData)
      this.modalData = Object.assign({}, detail, data.rowData)
      this.$nextTick().then(e => {
        this.$bvModal.show(id)
      })
    },
    getDetail (method, api, param, obj = null) {
      return this.$_api[method](api, param).then(res => {
        return obj ? res.data[obj] || res.data : res.data
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    copyValue (obj = null) {
      if (!obj) {
        this.$set(this.modalData, 'detail', { ...this.modalData.detail, ...this.modalData.sync.data_recaps })
      }
    },
    submitData (modalID) {
      const api = 'custom/presence-on-periode-recaps'
      this.$_api.update(api, this.modalData.detail).then(res => {
        this.mainKey++
        this.$_alert.success(null, 'Detail Rincian berhasil diperbarui')
      }).catch((err) => {
        this.$_alert.error(err)
      })
    }
  }
}
</script>



